// display page
.display-img {
  filter: grayscale(1) brightness(50%);
  background-image: url("../assets/pictures.jpg");
  opacity: 1;
  position: absolute;
  background-size: cover;
  top: 0;
  width: 100%;
  height: 101%;
  z-index: -1;
  transition: all 0.5s ease;
}

.letters_span {
  font-size: 50px;
  opacity: 0;
  margin-bottom: -100px;
  color: white;
  text-transform: capitalize;
}

.letters {
  position: absolute;
  left: 40px;
  top: 0;
}

.link_nostyle {
  text-decoration: none;
}

.link-container {
  position: absolute;
  right: 250px;
  top: 10px;
}

.li-items {
  text-align: center;
  width: 100px;
  opacity: 0;
  color: white;
  position: absolute;
  right: 20px;
  top: 9px;
  border-bottom: 1px solid white;
  padding: 5px 10px;
}

.li-items > a {
  color: white;
  text-decoration: none;
  font-weight: 700;
}

.nav_wishlist {
  position: absolute;
  top: 20px;
  right: 4%;
  font-size: 32px;
  color: #a31b16;
}

@media only screen and (max-width: 450px) {
  .letters {
    position: absolute;
    left: 10px;
    top: 12px;
  }
  .letters_span {
    font-size: 20px;
    margin-bottom: 0;
    opacity: 1;
  }
  .link-container {
    position: absolute;
    left: 50%;
    top: 8%;
  }
  #inspiration {
    display: none;
  }
  .nav_wishlist {
    top: 12px;
    right: 5%;
    font-size: 32px;
    color: #a31b16;
  }
}
